
@import url(./assets/css/reset.css);
#app .router-view {
  box-sizing: border-box;
  width: 100%;
  height: 550px;
  overflow-y: auto;
  padding-left: 200px;
  opacity: 1;
}
#app .router-view.transfrom-enter, #app .router-view.transform-leave-active {
    opacity: 0;
}
#app .router-view.transfrom-enter-active, #app .router-view.transform-leave-active {
    transition: opacity .2s ease-in;
}
#all .all-view {
  padding: 10px 30px;
  list-style: none;
}
#all .all-view h2 {
    font-weight: 400;
    padding: 30px 0 10px 8px;
}
#all .all-view .divider {
    width: 100%;
    height: 5px;
    margin-left: 8px;
    border-radius: 4px;
    background-image: linear-gradient(to right, #f60, blue, red);
}
#all .all-view .content {
    padding-top: 20px;
    position: relative;
}
#all .all-view .content li.thum_view {
      position: relative;
      float: left;
      width: 75px;
      height: 90px;
      margin: 8px;
}
#all .all-view .content li.thum_view .cover {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
        text-align: center;
        background-color: rgba(0, 0, 0, 0.7);
        opacity: 0;
        transition: opacity .3s;
        cursor: pointer;
        line-height: 90px;
}
#all .all-view .content li.thum_view:hover .cover {
        color: #fff;
        opacity: 1;
}
#all .all-view .content li.thum_view img, #all .all-view .content li.thum_view video {
        width: 100%;
        height: 100%;
}
#all .all-view .content li.list_view {
      position: relative;
      list-style-type: decimal;
      width: 100%;
      text-align: left;
      margin: 10px 8px;
      padding: 7px 0;
      line-height: 10px;
      background-color: #f2f2f2;
      color: #000;
}
#all .all-view .content li.list_view img {
        vertical-align: top;
        margin-left: 10px;
        width: 30px;
        height: 30px;
        display: inline-block;
}
#all .all-view .content li.list_view .rightBtn {
        position: absolute;
        right: 40px;
        top: 17px;
        display: inline-block;
}
#all .all-view .content li.list_view .rightBtn:hover {
          color: red;
}
#all .all-view .content li.list_view p {
        vertical-align: top;
        height: 30px;
        line-height: 30px;
        margin-left: 10px;
        display: inline-block;
}
#all .all-view .content li.list_view .createdAt {
        position: absolute;
        right: 180px;
}
#all .all-view .content li.list_view:hover {
        cursor: pointer;
        background-color: rgba(0, 0, 0, 0.1);
}



